import { calculateAgeInsights } from "./ageInsights.js"; //     

/**
 * Gemini API   
 */
export async function generateGeminiInsights(stats, apiKey, csvData = null) {
    //    
    if (!stats.ageInsights) {
        if (csvData && Array.isArray(csvData)) {
            console.log(' CSV     ...');
            console.log(' CSV  :', csvData.length);
            stats.ageInsights = calculateAgeInsights(csvData);
            console.log(' CSV    :', stats.ageInsights);
            console.log('   :', Object.keys(stats.ageInsights));
        } else if (stats.rows && Array.isArray(stats.rows)) {
            console.log(' rows     ...');
            console.log(' rows  :', stats.rows.length);
            stats.ageInsights = calculateAgeInsights(stats.rows);
            console.log(' rows    :', stats.ageInsights);
            console.log('   :', Object.keys(stats.ageInsights));
        } else {
            console.log('      .');
            console.log(' csvData:', csvData);
            console.log(' stats.rows:', stats.rows);
        }
    } else {
        console.log('     :', stats.ageInsights);
    }
    
    const prompt = createAnalysisPrompt(stats);
    
    try {
      const response = await fetch(
      `https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent?key=${apiKey}`,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            contents: [{
              parts: [{
                text: prompt
              }]
            }],
            generationConfig: {
              temperature: 0.2,
              maxOutputTokens: 8000,
              topP: 0.9,
              topK: 1
            }
          })
        }
      );
  
      if (!response.ok) {
      let errorMessage = `API Error: ${response.status}`;
      
      // Response body      clone text  
      const responseText = await response.text();
      
      try {
        const errorData = JSON.parse(responseText);
        const apiError = errorData.error?.message || '   ';
        errorMessage += ` - ${apiError}`;
        
        // 429  ( )  
        if (response.status === 429) {
          console.error(' API   -   :', JSON.stringify(errorData, null, 2));
          console.error('  :', errorData.error);
          
          //     
          const quotaViolations = errorData.error?.details?.find(d => d['@type'] === 'type.googleapis.com/google.rpc.QuotaFailure')?.violations || [];
          const quotaDetails = quotaViolations.map(v => `- ${v.quotaMetric || '  '}: limit ${v.quotaId || 'N/A'}`).join('\n');
          
          //   
          const retryInfo = errorData.error?.details?.find(d => d['@type'] === 'type.googleapis.com/google.rpc.RetryInfo');
          const retryDelay = retryInfo?.retryDelay ? `  : ${retryInfo.retryDelay}` : '';
          
          throw new Error(`API   (429)\n\n    .\n\n :\n${apiError}\n\n :\n${quotaDetails || '  '}\n${retryDelay ? retryDelay + '\n' : ''}\n :\n1. Google AI Studio  : https://ai.dev/usage?tab=rate-limit\n2.    (  )\n3.    ( 24 )\n4.  API  \n\n     0      .`);
        }
      } catch (jsonError) {
        // JSON   
        if (response.status === 429) {
          console.error(' 429  -  :', responseText.substring(0, 500));
          throw new Error('API   (429)\n\n    .\n\n :\n1. Google AI Studio  \n2.   \n3.   ');
        }
        console.error('HTML :', responseText.substring(0, 200));
        errorMessage += ` - HTML  . API  .`;
      }
      throw new Error(errorMessage);
    }

    // response.ok true  body   
    const responseText = await response.text();
    console.log('API :', responseText.substring(0, 200));
    
    let data;
    try {
      data = JSON.parse(responseText);
    } catch (parseError) {
      console.error('JSON  :', parseError);
      console.error(' :', responseText.substring(0, 500));
      throw new Error('API   JSON . API  .');
    }

    if (data.candidates && data.candidates[0] && data.candidates[0].content && data.candidates[0].content.parts && data.candidates[0].content.parts[0]) {
      return data.candidates[0].content.parts[0].text;
    } else {
      console.error('API   :', data);
      throw new Error('API    .');
    }
    } catch (error) {
      console.error('Gemini API :', error);
      throw error;
    }
  }
  
