/**
 *    (Gemini API + Fallback)
 */
export async function generateHybridReport(stats, apiKey, csvData = null) {
  // API  
  if (!apiKey) {
    console.error(' API  .');
    console.warn(' Fallback  .');
    return generateFallbackReport(stats);
  }
  
  try {
    console.log(' Gemini API   ...');
    console.log(' API  :', apiKey ? `${apiKey.substring(0, 10)}...` : '');
    const result = await generateGeminiInsights(stats, apiKey, csvData);
    console.log(' Gemini API !');
    return result;
  } catch (error) {
    console.error(' Gemini API :', error);
    console.error('  :', error.message);
    console.error('  :', error.stack);
    console.warn(' Fallback  .');
    
    // Fallback   (   )
    return generateFallbackReport(stats);
  }
}

/**
 * Fallback  
 */
function generateFallbackReport(stats) {
  const residenceRate = ((stats.residenceCount / stats.total) * 100).toFixed(1);
  const investmentRate = ((stats.investmentCount / stats.total) * 100).toFixed(1);
  const totalLoanAmount = stats.totalLoanAmount ? (stats.totalLoanAmount / 100000000).toFixed(1) : '0';
  const averageLoanAmount = stats.averageLoanAmount ? (stats.averageLoanAmount / 100000000).toFixed(1) : '0';

  return `#    (Fallback)
  
  ##    
  
- ** **: ${stats.residenceCount} (${residenceRate}%)
- ** **: ${stats.investmentCount} (${investmentRate}%)
- ** **: ${totalLoanAmount}
- **  **: ${averageLoanAmount}
  
  ##   
  
  ###  
${stats.ageGroups ? Object.entries(stats.ageGroups).map(([age, count]) => `- ${age}: ${count}`).join('\n') : ' '}
  
  ###  
${stats.genderGroups ? Object.entries(stats.genderGroups).map(([gender, count]) => `- ${gender}: ${count}`).join('\n') : ' '}
  
  ##    
  
  ### / 
  |  |  |  |
  |------|--------|------|
|  | ${stats.residenceCount} | ${residenceRate}% |
|  | ${stats.investmentCount} | ${investmentRate}% |
  
  ##    
  
  ###   
- ** **: ${stats.loanCount || 0}
- ****: ${stats.noLoanCount || 0}
  
  ###  
- ** **: ${totalLoanAmount}
- ** **: ${averageLoanAmount}
  
  ##    
  
  ###  
${stats.areaGroups ? Object.entries(stats.areaGroups).map(([area, count]) => `- ${area}: ${count}`).join('\n') : ' '}
  
  ###   
${stats.holdingGroups ? Object.entries(stats.holdingGroups).map(([period, count]) => `- ${period}: ${count}`).join('\n') : ' '}
  
  ###   
${stats.transferReasons ? Object.entries(stats.transferReasons).map(([reason, count]) => `- ${reason}: ${count}`).join('\n') : ' '}

  ##   
  
- ** **: ${new Date().toLocaleDateString()}
- ** **: ${stats.total}
- ** **: 

---

*     .*`;
}

/**
 *  
 */
export function downloadAsMarkdown(content, filename = 'report.md') {
  const blob = new Blob([content], { type: 'text/markdown' });
    const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = filename;
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }
  
  /**
   * HTML 
   */
export function downloadAsHTML(content, filename = 'report.html') {
  const htmlContent = `
  <!DOCTYPE html>
  <html lang="ko">
  <head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>  </title>
    <style>
        body { font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif; line-height: 1.6; max-width: 800px; margin: 0 auto; padding: 20px; }
        h1, h2, h3 { color: #2d3748; }
        table { width: 100%; border-collapse: collapse; margin: 20px 0; }
        th, td { border: 1px solid #e2e8f0; padding: 12px; text-align: left; }
        th { background-color: #f7fafc; font-weight: 600; }
        code { background-color: #f7fafc; padding: 2px 4px; border-radius: 3px; }
        blockquote { border-left: 4px solid #4299e1; margin: 20px 0; padding-left: 20px; color: #4a5568; }
    </style>
  </head>
  <body>
    ${content.replace(/\n/g, '<br>')}
  </body>
</html>`;
  
  const blob = new Blob([htmlContent], { type: 'text/html' });
    const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = filename;
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }