/**
 * -only localStorage   
 * 
 * :   /.
 *    /HttpOnly  .
 */

const SESSION_KEY = 'session';

/**
 *   localStorage 
 * @param {Object} session -  
 * @param {string} session.username - 
 * @param {string} session.role -  (: 'user')
 * @param {number} session.ttlMs -  () (: 1)
 */
export const setSession = ({ username, role = 'user', ttlMs = 60 * 60 * 1000 }) => {
  const session = {
    username,
    role,
    expiresAt: Date.now() + ttlMs
  };
  
  try {
    localStorage.setItem(SESSION_KEY, JSON.stringify(session));
  } catch (error) {
    console.error('  :', error);
  }
};

/**
 * localStorage   
 * @returns {Object|null}    null
 */
export const getSession = () => {
  try {
    const sessionStr = localStorage.getItem(SESSION_KEY);
    if (!sessionStr) return null;
    
    const session = JSON.parse(sessionStr);
    
    //   
    if (Date.now() > session.expiresAt) {
      clearSession();
      return null;
    }
    
    return session;
  } catch (error) {
    console.error('  :', error);
    clearSession();
    return null;
  }
};

/**
 * localStorage   
 */
export const clearSession = () => {
  try {
    localStorage.removeItem(SESSION_KEY);
  } catch (error) {
    console.error('  :', error);
  }
};

/**
 *    
 * @param {number} ttlMs -   () (: 1)
 */
export const touchSession = (ttlMs = 60 * 60 * 1000) => {
  const session = getSession();
  if (session) {
    setSession({
      username: session.username,
      role: session.role,
      ttlMs
    });
  }
};

/**
 *   
 * @returns {boolean}  
 */
export const isAuthenticated = () => {
  return !!getSession();
};
