import Papa from 'papaparse';

/**
 *     ( CSV  )
 */
const resolveAssetUrl = (pathOrUrl) => {
  if (!pathOrUrl) return pathOrUrl;
  //   URL  
  if (/^https?:\/\//i.test(pathOrUrl)) return pathOrUrl;
  const base = process.env.PUBLIC_URL || '';
  //     PUBLIC_URL  
  const suffix = pathOrUrl.startsWith('/') ? pathOrUrl : `/${pathOrUrl}`;
  return `${base}${suffix}`;
};

export const loadStaticProjectData = async (dataFile) => {
  try {
    console.log('    :', dataFile);
    const response = await fetch(resolveAssetUrl(dataFile));
    
    if (!response.ok) {
      throw new Error(`   : ${dataFile}`);
    }
    
    const csvText = await response.text();
    
    return new Promise((resolve, reject) => {
      Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        complete: (results) => {
          console.log('    :', results.data.length, '');
          console.log(' CSV   :', results.data.slice(0, 3));
          console.log(' CSV  :', results.errors);
          
          //    (577)
          console.log('   :', results.data.length, '');
          resolve(results.data);
        },
        error: (error) => {
          console.error('    :', error);
          reject(error);
        }
      });
    });
  } catch (error) {
    console.error('     :', error);
    throw error;
  }
};

/**
 *     (  CSV )
 */
export const loadProcessedProjectData = async (dataFile) => {
  try {
    console.log('    :', dataFile);
    
    const response = await fetch(resolveAssetUrl(dataFile));
    if (!response.ok) {
      throw new Error(`    : ${dataFile}`);
    }
    
    const csvText = await response.text();
    
    return new Promise((resolve, reject) => {
      Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        complete: (results) => {
          console.log('    :', results.data.length, '');
          console.log('   :', results.data.slice(0, 3));
          console.log(' CSV  :', results.errors);
          resolve(results.data);
        },
        error: (error) => {
          console.error('    :', error);
          reject(error);
        }
      });
    });
  } catch (error) {
    console.error('     :', error);
    throw error;
  }
};

/**
 *     (LocalStorage )
 */
export const loadUserProjectData = (project) => {
  try {
    console.log('    :', project.name);
    
    if (!project.data) {
      throw new Error('  .');
    }
    
    console.log('    :', project.data.length, '');
    return project.data;
  } catch (error) {
    console.error('     :', error);
    throw error;
  }
};

/**
 *     (LocalStorage)
 */
export const loadUserProjects = () => {
  try {
    const userProjects = JSON.parse(localStorage.getItem('userProjects') || '[]');
    console.log('    :', userProjects.length, '');
    return userProjects;
  } catch (error) {
    console.error('     :', error);
    return [];
  }
};

/**
 *    (LocalStorage)
 */
export const saveUserProject = (project) => {
  try {
    const userProjects = loadUserProjects();
    const updatedProjects = [...userProjects, project];
    localStorage.setItem('userProjects', JSON.stringify(updatedProjects));
    console.log('    :', project.name);
    return true;
  } catch (error) {
    console.error('    :', error);
    return false;
  }
};

/**
 *   
 */
export const deleteUserProject = (projectId) => {
  try {
    const userProjects = loadUserProjects();
    const updatedProjects = userProjects.filter(project => project.id !== projectId);
    localStorage.setItem('userProjects', JSON.stringify(updatedProjects));
    console.log('    :', projectId);
    return true;
  } catch (error) {
    console.error('    :', error);
    return false;
  }
};

/**
 * DB   
 */
export const loadProjectDataFromDB = async (project) => {
  try {
    const { loadBuildingDataFromDB } = require('./api');
    console.log(' DB   :', project.name);
    
    const buildingId = project.buildingId || project.id?.replace('db-', '');
    if (!buildingId) {
      throw new Error(' ID .');
    }
    
    const data = await loadBuildingDataFromDB(buildingId);
    console.log(' DB   :', data.length, '');
    return data;
  } catch (error) {
    console.error(' DB   :', error);
    throw error;
  }
};

/**
 *    (   )
 */
export const loadProjectData = async (project) => {
  // DB  
  if (project.type === 'db') {
    return await loadProjectDataFromDB(project);
  }
  
  //  
  if (project.type === 'static') {
    return await loadStaticProjectData(project.dataFile);
  } else if (project.type === 'user') {
    return loadUserProjectData(project);
  } else if (project.type === 'processed') {
    return await loadProcessedProjectData(project.dataFile);
  } else {
    throw new Error('    .');
  }
};

