import React from 'react';
import './ProjectCard.css';

const ProjectCard = ({ project, onSelect, onDelete }) => {
  const handleClick = () => {
    onSelect(project);
  };

  const handleDelete = (e) => {
    e.stopPropagation(); //    
    if (window.confirm(`"${project.name}"  ?`)) {
      onDelete(project.id);
    }
  };

  const resolveImageSrc = (src) => {
    if (!src) return src;
    if (/^https?:\/\//i.test(src)) return src;
    const base = process.env.PUBLIC_URL || '';
    const suffix = src.startsWith('/') ? src : `/${src}`;
    return `${base}${suffix}`;
  };

  return (
    <div className="project-card" onClick={handleClick}>
      <div className="project-card-image">
        <img 
          src={resolveImageSrc(project.image)} 
          alt={project.name}
          onError={(e) => {
            console.error('  :', project.image);
            e.target.src = 'data:image/svg+xml;base64,';
          }}
        />
        <div className="project-card-type">
          {project.type === 'static' ? '' : ''}
        </div>
      </div>
      
      <div className="project-card-content">
        <div className="project-card-header">
          <h3 className="project-card-title">{project.name}</h3>
          {project.type === 'user' && (
            <button 
              className="project-card-delete"
              onClick={handleDelete}
              title=" "
            >
              
            </button>
          )}
        </div>
        
        <p className="project-card-address">{project.address}</p>
        {project.description && (
          <p className="project-card-description">{project.description}</p>
        )}
        
        <div className="project-card-footer">
          <span className="project-card-arrow"></span>
        </div>
      </div>
    </div>
  );
};

export default ProjectCard;
