import React from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import './ChartCard.css';

const AgeDistribution = ({ data, total }) => {
  const ageData = Object.entries(data || {})
    .map(([range, count]) => ({ 
      range, 
      count,
      percentage: total > 0 ? ((count / total) * 100).toFixed(1) : 0
    }))
    .sort((a, b) => {
      // '' '' 
      const order = ['', '20', '30', '40', '50', '60', '70', '80', '90', '90 ', '', ''];
      return order.indexOf(a.range) - order.indexOf(b.range);
    });

  return (
    <div className="chart-card">
      <h3 className="chart-card__title"></h3>
      <p className="chart-card__subtitle"> {total}</p>

      <ResponsiveContainer width="100%" height={300}>
        <BarChart data={ageData}>
          <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
          <XAxis dataKey="range" tick={{ fontSize: 12 }} />
          <YAxis tick={{ fontSize: 12 }} />
          <Tooltip
            contentStyle={{
              backgroundColor: '#000000',
              color: '#ffffff',
              border: 'none',
              borderRadius: '8px',
              fontSize: '12px'
            }}
          />
          <Bar dataKey="count" fill="#10b981" />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default React.memo(AgeDistribution);
