import React, { useState } from 'react';
import { 
  generateHybridReport, 
  downloadAsMarkdown, 
  downloadAsHTML 
} from '../utils/geminiReportGenerator';
import ReportPreview from './ReportPreview';

//
// console.log(" process.env keys:", Object.keys(process.env));
// console.log(" GEMINI:", process.env.REACT_APP_GEMINI_API_KEY);
// .env  API  
const GEMINI_API_KEY = process.env.REACT_APP_GEMINI_API_KEY;


export default function ReportGenerator({ statsData, activeTab, csvData }) {
  const [showReport, setShowReport] = useState(false);
  const [reportContent, setReportContent] = useState('');
  const [isGenerating, setIsGenerating] = useState(false);

  //  
  // console.log(' ReportGenerator props:', { statsData, activeTab, csvData });
  // console.log(' statsData keys:', statsData ? Object.keys(statsData) : '');
  if (statsData && statsData[activeTab]) {
    // console.log(' activeTab :', statsData[activeTab]);
    // console.log(' ageGroups:', statsData[activeTab].ageGroups);
    // console.log(' transferReasons:', statsData[activeTab].transferReasons);
    // console.log(' areaGroups:', statsData[activeTab].areaGroups);
    // console.log(' holdingGroups:', statsData[activeTab].holdingGroups);
    // console.log(' totalLoanAmount:', statsData[activeTab].totalLoanAmount);
    // console.log(' averageLoanAmount:', statsData[activeTab].averageLoanAmount);
    // console.log(' ageInsights:', statsData[activeTab].ageInsights);
    
    // ageInsights  
    if (statsData[activeTab].ageInsights) {
      // console.log(' ReportGenerator ageInsights  :');
      Object.entries(statsData[activeTab].ageInsights).forEach(([age, insight]) => {
        // console.log(` ${age}:`, {
        //   loanRate: insight.loanRate,
        //   avgLoan: insight.avgLoan,
        //   residenceRate: insight.residenceRate,
        //   seizureRate: insight.seizureRate
        // });
      });
    } else {
      // console.log(' ReportGenerator ageInsights  !');
    }
  } else {
    // console.log(' ReportGenerator activeTab  ');
    // console.log(' statsData:', statsData);
    // console.log(' activeTab:', activeTab);
  }

  //  
  const handleGenerateReport = async () => {
    console.log('===     ===');
    console.log(' statsData:', statsData);
    console.log(' activeTab:', activeTab);
    console.log(' csvData :', csvData ? csvData.length : '');
    
    //      (  )
    const reportStatsKey = '';
    
    //   
    if (!statsData || !statsData[reportStatsKey]) {
      console.error('   :', statsData);
      alert('   .\n   .');
      return;
    }
    
    // API  
    if (!GEMINI_API_KEY) {
      console.error(' API  ');
      alert(' API   .\n.env  REACT_APP_GEMINI_API_KEY .');
      return;
    }
    
    console.log('  API  Gemini API ');
    console.log(' API  :', GEMINI_API_KEY ? `${GEMINI_API_KEY.substring(0, 10)}...` : '');
    console.log('  :', statsData);

    setIsGenerating(true);
    
    try {
      // AI     (   )
      console.log(' generateHybridReport    :');
      console.log(' statsData[]:', statsData[reportStatsKey]);
      console.log(' API  :', GEMINI_API_KEY ? `${GEMINI_API_KEY.substring(0, 10)}...` : '');
      
      const report = await generateHybridReport(
        statsData[reportStatsKey], 
        GEMINI_API_KEY,
        csvData //  CSV   ( )
      );
      
      console.log('  :', report ? report.substring(0, 200) : '');
      console.log('  :', report ? report.length : 0);
      
      // Fallback  
      if (report && report.includes('#    (Fallback)')) {
        console.warn(' Fallback  . Gemini API  .');
        alert(' Gemini API     .\n\n (F12)   .\n\n:\n1. .env  REACT_APP_GEMINI_API_KEY  \n2. API  \n3.   \n4.     ');
      }
      
      setReportContent(report);
      setShowReport(true);
    } catch (error) {
      console.error('  :', error);
      console.error(' :', error.message);
      console.error(' :', error.stack);
      
      //   
      let errorMessage = '    .';
      
      if (error.message.includes('API   ')) {
        errorMessage = ' API   .\n.env  REACT_APP_GEMINI_API_KEY .';
      } else if (error.message.includes('API key') || error.message.includes('API_KEY_INVALID')) {
        errorMessage = ' API   .\n.env  API  .';
      } else if (error.message.includes('quota') || error.message.includes('RATE_LIMIT')) {
        errorMessage = '  (1,500) .\n  .';
      } else if (error.message.includes('network') || error.message.includes('fetch')) {
        errorMessage = '   .\n  .';
      } else if (error.message.includes('HTML ') || error.message.includes('JSON')) {
        errorMessage = ' API   API   .\n.env  API  .';
      } else if (error.message.includes('Gemini API  ')) {
        errorMessage = ` ${error.message}\n\n (F12)   .`;
      } else {
        errorMessage = ` ${error.message}`;
      }
      
      alert(errorMessage);
    } finally {
      setIsGenerating(false);
    }
  };

  //  
  const handleDownloadReport = (format) => {
    //       
    const filename = `_`;
    
    if (format === 'markdown') {
      downloadAsMarkdown(reportContent, filename);
    } else if (format === 'html') {
      downloadAsHTML(reportContent, filename);
    }
  };

  return (
    <>
      {/*    */}
      <div className="bg-gradient-to-r from-emerald-500 to-teal-600 rounded-xl shadow-xl p-8 text-white">
        <div className="flex items-center justify-between">
          <div className="flex-1">
            <h2 className="text-3xl font-bold mb-2 flex items-center gap-3">
              <span className="text-4xl"></span>
              AI  
            </h2>
            <p className="text-emerald-100 text-lg">
              Google Gemini       
            </p>
            <div className="mt-4 flex items-center gap-4 text-sm">
              <div className="flex items-center gap-2 bg-white/20 px-3 py-1.5 rounded-full">
                <span></span>
                <span> 30 </span>
              </div>
              <div className="flex items-center gap-2 bg-white/20 px-3 py-1.5 rounded-full">
                <span></span>
                <span>  </span>
              </div>
              <div className="flex items-center gap-2 bg-white/20 px-3 py-1.5 rounded-full">
                <span></span>
                <span> </span>
              </div>
            </div>
          </div>
          
          <button
            onClick={handleGenerateReport}
            disabled={isGenerating}
            className={`ml-8 px-8 py-4 rounded-xl font-bold text-lg transition-all transform ${
              isGenerating
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-white text-emerald-600 hover:bg-emerald-50 hover:scale-105 shadow-2xl'
            }`}
          >
            {isGenerating ? (
              <span className="flex items-center gap-3">
                <svg className="animate-spin h-6 w-6" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none"/>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"/>
                </svg>
                <span>AI  ...</span>
              </span>
            ) : (
              <span className="flex items-center gap-3">
                <span className="text-2xl"></span>
                <span> </span>
              </span>
            )}
          </button>
        </div>

        {/*     */}
        <div className="mt-6 pt-6 border-t border-emerald-400/30">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <div className="bg-white/20 px-4 py-2 rounded-lg">
                <div className="text-emerald-100 text-xs mb-1"> </div>
                <div className="text-white text-xl font-bold"> </div>
              </div>
              <div className="bg-white/20 px-4 py-2 rounded-lg">
                <div className="text-emerald-100 text-xs mb-1"> </div>
                <div className="text-white text-xl font-bold">{statsData['']?.total || 0}</div>
              </div>
              <div className="bg-white/20 px-4 py-2 rounded-lg">
                <div className="text-emerald-100 text-xs mb-1"> </div>
                <div className="text-white text-xl font-bold">
                  {statsData['']?.totalLoanAmount 
                    ? `${(statsData[''].totalLoanAmount / 100000000).toFixed(1)}`
                    : '0'
                  }
                </div>
              </div>
            </div>
            
            <div className="text-right">
              <div className="text-emerald-100 text-sm mb-1">  </div>
              <div className="flex gap-2 text-xs">
                <span className="bg-white/20 px-2 py-1 rounded"> </span>
                <span className="bg-white/20 px-2 py-1 rounded"> AI </span>
                <span className="bg-white/20 px-2 py-1 rounded"> </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*    */}
      {showReport && (
        <ReportPreview
          reportContent={reportContent}
          onClose={() => setShowReport(false)}
          onDownload={handleDownloadReport}
        />
      )}
    </>
  );
}