import React from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import './ChartCard.css';

const HoldingPeriod = ({ data, total, selectedAgeGroup, setSelectedAgeGroup, availableAgeGroups }) => {
  //  4  
  const reclassifiedData = {};
  
  Object.entries(data || {}).forEach(([period, count]) => {
    const years = parseFloat(period.replace('', '').replace(' ', '').replace(' ', ''));
    
    if (years < 3) {
      reclassifiedData['3 '] = (reclassifiedData['3 '] || 0) + count;
    } else if (years >= 3 && years < 7) {
      reclassifiedData['3~7'] = (reclassifiedData['3~7'] || 0) + count;
    } else if (years >= 7 && years < 15) {
      reclassifiedData['7~15'] = (reclassifiedData['7~15'] || 0) + count;
    } else {
      reclassifiedData['15 '] = (reclassifiedData['15 '] || 0) + count;
    }
  });

  const holdingData = Object.entries(reclassifiedData)
    .map(([period, count]) => ({ period, count }))
    .sort((a, b) => {
      const order = ['3 ', '3~7', '7~15', '15 '];
      return order.indexOf(a.period) - order.indexOf(b.period);
    });

  return (
    <div className="chart-card">
      <h3 className="chart-card__title"> </h3>
      <p className="chart-card__subtitle"> {total}</p>
      
      <div className="chart-card__tabs">
        {(availableAgeGroups || []).map((ageGroup) => (
          <button
            key={ageGroup}
            onClick={() => setSelectedAgeGroup(ageGroup)}
            className={`chart-card__tab ${
              selectedAgeGroup === ageGroup ? 'chart-card__tab--active' : ''
            }`}
          >
            {ageGroup}
          </button>
        ))}
      </div>

      <ResponsiveContainer width="100%" height={250}>
        <BarChart data={holdingData}>
          <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
          <XAxis dataKey="period" tick={{ fontSize: 12 }} />
          <YAxis tick={{ fontSize: 12 }} />
          <Tooltip
            contentStyle={{
              backgroundColor: '#000000',
              color: '#ffffff',
              border: 'none',
              borderRadius: '8px',
              fontSize: '12px'
            }}
          />
          <Bar dataKey="count" fill="#f59e0b" />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default HoldingPeriod;
